import axios from 'axios';

const ToThousands = (str) => {
    var newStr = "";
    var count = 0;
    if (str.indexOf(".") === -1) {
        for (let i = str.length - 1; i >= 0; i--) {
            if (count % 3 === 0 && count !== 0) {
                newStr = str.charAt(i) + "," + newStr;
            } else {
                newStr = str.charAt(i) + newStr;
            }
            count++;
        }
        str = newStr;
        return str;
    }
    else {
        for (let i = str.indexOf(".") - 1; i >= 0; i--) {
            if (count % 3 === 0 && count !== 0) {
                newStr = str.charAt(i) + "," + newStr;
            } else {
                newStr = str.charAt(i) + newStr;
            }
            count++;
        }
        str = newStr + (str + "00").substr((str + "00").indexOf("."), 3);
        return str;
    }
}

const SecondToDay = (time) => {
    return Math.ceil(time / (60 * 60 * 24));
    // return (time / (60 * 60 * 24)).toFixed(2);
}

const handleApy = (totalRewardNum, totalStakeNum, periodNum, thresholdNum) => {


    let totalStake = parseInt(totalStakeNum);
    let totalReward = parseInt(totalRewardNum);
    let threshold = parseInt(thresholdNum);
    let period = parseInt(periodNum);


    let apy;
    if (totalReward * totalStake * period < 1 || totalStake < threshold) {
        apy = totalReward / threshold * (365 / (period / (60 * 60 * 24))) * 100;
    }
    else {
        apy = totalReward / totalStake * (365 / (period / (60 * 60 * 24))) * 100;
    }

    return apy.toFixed(2);
}

const handleWidth = (totalStake, threshold) => {

    if (parseInt(totalStake) >= parseInt(threshold)) {
        return 100;
    }

    return (parseInt(totalStake) / parseInt(threshold)) * 100;
}



const IPFS_BASES = [
    'https://10.via0.com/ipfs',
    'https://ipfs.io/ipfs',
    'https://ipfs.leiyun.org/ipfs',
    'https://cloudflare-ipfs.com/ipfs',
  ];

async function agumentedIpfsGet(hash) {
    const promises = IPFS_BASES.map(ipfsBase => axios.get(`${ipfsBase}/${hash}`));

    // if (Promise.any) {
    //     return await Promise.any(promises);
    // } else {
    //     console.warn('No Promise.any, fallback to p-any');
    //     return await pAny(promises);
    // }

    return await Promise.any(promises);
}

async function getServiceProviderInfo(uri) {
    const hash = uri.replace('/ipfs/', '');
    const resp = await agumentedIpfsGet(hash);
    return resp.data;
}

const PublicObj = {
    ToThousands,
    SecondToDay,
    handleApy,
    handleWidth,
    getServiceProviderInfo
}

export default PublicObj;
