import erc20 from "./erc20";
let balanceBefore = null;

const initBalance = async (state,dispatch) =>{

    const {web3Instance,erc20contract,Account,mybalanceState} = state;
    const getBalance = async () =>{
       await erc20.getBalanceOf(erc20contract, Account).then((data)=>{
            balanceBefore = web3Instance.web3Instance.utils.fromWei(data);
            dispatch({type: 'SET_BALANCE',payload:balanceBefore});
        });
    }

    if (mybalanceState !== 'BALANCE_INIT') return;
    getBalance();


}

export default initBalance;
