import React, {useContext, useReducer} from 'react';

import initContract from './initContract';
import initErc20 from './initErc20';
import initBalance from './initBalance';

import reducer from './reducer';
import INIT_STATE from './initState';

import Web3Modal from "web3modal";
const { network } = window.GeneralConfig;

const Web3 = require('web3');
const initState = {...INIT_STATE};

const Web3Context = React.createContext();

const connect = async (state, dispatch) => {
    const {web3InstanceState} = state;

    if (web3InstanceState!=null) return;

    dispatch({type: 'CONNECT_INIT'});

    const providerOptions = {};
    const web3Modal = new Web3Modal({
        network,
        cacheProvider: true,
        providerOptions
    });

    dispatch({type: 'CONNECT_INIT'});
    const provider = await web3Modal.connect();

    const web3Instance = new Web3(provider);

    if(web3Instance){
        dispatch({type: 'CONNECT', payload: { web3Instance }});

    }

};

const Web3ContextProvider = (props) => {
    const [state, dispatch] = useReducer(reducer, initState);
    console.log("=====state=====",state);
    const { web3Instance,Account } = state;
    if(web3Instance == null ) {
        connect(state, dispatch);
    }
    initContract(state, dispatch);
    initErc20(state, dispatch);

    if(Account != null ) {
        initBalance(state, dispatch);
    }

    return <Web3Context.Provider value={{state,dispatch}}>
        {props.children}
    </Web3Context.Provider>;
};

const useWeb3 = () => ({...useContext(Web3Context)});
export {Web3ContextProvider, useWeb3};
