import AddressObj from "./addressConfig";

let maincontract = null;
let loadMain = false;

const {stakingABI, stakingAddress} = AddressObj;

export default async function initContract(state, dispatch) {
    const {web3Instance,maincontractState} = state;

    if(web3Instance == null || web3Instance.web3Instance == null) return;

    const asyncLoadMain = async () => {
        try {
            maincontract = await new web3Instance.web3Instance.eth.Contract(stakingABI, stakingAddress);

            dispatch({type: 'SET_MAINCONTRACT', payload: maincontract});
        } catch (e) {
            console.error(e);
            dispatch({type: 'MAINCONTRACT_ERROR'});
        }
    };
    if (maincontractState !== 'LOAD_MAINCONTRACT') return;
    if (loadMain) return dispatch({type: 'SET_MAINCONTRACT', payload: maincontract});
    loadMain = true;
    asyncLoadMain();
}
