import React, {useState, useEffect} from 'react';

import styled from "styled-components";
import {Grid } from '@geist-ui/react';
import MyItem from "./components/myItem";

import {useWeb3} from "./api/contracts";
import services from "./api/services";
import Loading from "./components/loading";

const Row = styled.div`
  margin: 112px 0 92px;
    padding: 0 24px;
  @media (max-width: 768px) {
    margin-bottom: 24px;
  }
`;


export default function My(props) {

    const {state,dispatch} = useWeb3();
    const { maincontract,web3Instance, Account,loading} = state;


    // const [list, setList] = useState([]);
    const [ setList] = useState([]);
    const [refresh, setRefresh] = useState(false);

    useEffect(() => {
        if(maincontract==null || web3Instance == null) return;
        getServicesList()
    }, [maincontract,web3Instance,Account,refresh]);
    const getServicesList = async () =>{
        dispatch({type: 'SET_LOADING',payload:{tips:"Get My Contribution List..."}});
        services.getServicesList(maincontract, Account).then((data)=>{
            dispatch({type: 'SET_LOADING',payload:null});
            let arr = data.filter(item=> parseInt(item.myStake) > 0);
            let dataAfter =  arr.slice().sort((a, b)=>  parseInt(a.created) > parseInt(b.created) ? -1 : 1);
            setList(dataAfter)
        });
    };
    const handleRefresh = () =>{
        setRefresh(!refresh)
    }
    useEffect(() => {
        if(maincontract==null || web3Instance == null) return;
        getServicesList()
    }, [maincontract,web3Instance,Account,refresh]);
    useEffect(() => {
        if(maincontract==null || web3Instance == null) return;
        getServicesList()
    }, []);

    const item={

            spname:'polkaworld',
            logo:'',
            name:'',
            myStake:'',
            status:'',
            myTotal:'',
            myAvailable:'',
            myClaimed:'',
            threshold:'',
            created:'',
            period:'',
            grace:'',
            id:''
    }
    return (
        <div className="maincontain">
            {
                loading!= null && <Loading tips={loading.tips} />
            }
            <Row>
                <Grid.Container gap={4}>
                    {/*{*/}
                    {/*    list && list.map((item)=>(*/}
                    {/*        <Grid xs={24} md={8} key={`myList_${item.id}`}>*/}
                    {/*            <MyItem Itemdata={item} refresh={refresh} handleRefresh={handleRefresh}/>*/}
                    {/*        </Grid>*/}
                    {/*    ))*/}
                    {/*}*/}

                    <Grid xs={24} md={12} >
                        <MyItem Itemdata={item} refresh={refresh} handleRefresh={handleRefresh}/>
                    </Grid>
                </Grid.Container>
            </Row>
        </div>
    );
}
