export default function ListItem(props) {

    const { state } = useWeb3();
    const { web3Instance, Account } = state;

    const switchTime = (dateTime) => {
        const daytime = parseInt(dateTime) * 1000;
        var timestamp1 = new Date(daytime).toLocaleString("en");
        return timestamp1.split(",")[0];
    }

    const toThousands = (str) => {
        let balance = web3Instance.web3Instance.utils.fromWei(str);
        return PublicObj.ToThousands(balance);
    }

    const { openBox, openModal, Itemdata } = props;
    const { spname, logo, name, desc, share, status, created, period, totalReward, totalStake, threshold, id } = Itemdata;

    return (
        <Librdr>
            <div className={ `brdr status${status}`}>
                <LiLft>
                    <FirstRow>
                        <LftT>
                            <Avatar src={logo} width="28px" />
                            <Author>by {spname}</Author>
                        </LftT>
                    </FirstRow>
                    <div>
                        <Titles>
                            {/*#{id} */}
                            {name}
                        </Titles>
                        <ContentS>{desc}</ContentS>
                    </div>
                    <LastRow>
                        <Btn onClick={() => openModal(id, name)} disabled={status !== '0' || Account == null} >Stake</Btn>
                        <Rhta onClick={() => openBox(id)}><strong>Details</strong> <span /></Rhta>
                    </LastRow>
                </LiLft>
                <LiRht>
                    <div>
                        {
                            status === '1' && <ProgressDiv>
                                <img src={inProgress} alt="" />
                                <div className="rhtTips">Success</div>
                            </ProgressDiv>
                        }
                        {
                            status === '3' && <FailDiv>
                                <img src={failedImg} alt="" />
                                <div className="rhtTips">Failed</div>
                            </FailDiv>
                        }
                        {
                            status === '0' && <AuditDiv>
                                <img src={auditImg} alt="" />
                                <div className="rhtTips">Audit {switchTime(created)}</div>
                            </AuditDiv>
                        }
                        {
                            status === '2' && <EndDiv>
                                <img src={endingImg} alt="" />
                                <div className="rhtTips">Ending</div>
                            </EndDiv>
                        }
                    </div>
                    <div>
                        <Ul>
                            {/*<Li>*/}
                            {/*    <Star>*</Star>*/}
                            {/*    <Names>Estimate Reward</Names>*/}
                            {/*    <PercentS>{toThousands(totalReward)} ZHT</PercentS>*/}
                            {/*</Li>*/}
                            <Li>
                                <Star><img src={star} alt=""/></Star>
                                <Names>Share</Names>
                                <PercentS>{share}%</PercentS>
                            </Li>
                            <Li>
                                <Star><img src={star} alt=""/></Star>
                                <Names>Estimate ZHT</Names>
                                <PercentS>{PublicObj.handleApy(totalReward, totalStake, period, threshold)}%</PercentS>
                            </Li>
                        </Ul>
                    </div>
                    <div>
                        <PercentBg>
                            <Active style={{ width: PublicObj.handleWidth(totalStake, threshold) + '%' }} />
                        </PercentBg>
                        <Texts>
                            {/*<span>Period: {PublicObj.SecondToDay(period)} Days</span>*/}
                            <span>{toThousands(totalStake)} / {toThousands(threshold)} ZHT</span>
                        </Texts>
                    </div>
                </LiRht>


            </div>
        </Librdr>

    );

}
