import React, {useState, useEffect} from 'react';
import { useWeb3 } from "../api/contracts";
import styled from "styled-components";
import * as history from 'history';
import { Loading } from '@geist-ui/react'

import services from "../api/services";
import Accounts from '../api/Account';

import logo from '../assets/images/Logo.png';
import arrowBlack from '../assets/images/arrow_vi.svg';
import arrowWhite from '../assets/images/arrow_white.svg';
import accountImg from '../assets/images/add_normal.svg';
import balanceImg from '../assets/images/bal_normal.svg';
import IconImg from '../assets/images/menu_normal.svg';
import PublicObj from "../utils/publicJs";

const createHashHistory = history.createHashHistory();

const HeaderBg= styled.div`
  background: #000;
  width: 100vw;
  height: 80px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
  border-bottom:  1px solid #0BBF59;
`;
const HeaderFlex = styled.div`
  display: flex;
  justify-content: space-between;
  align-content: center;
  padding: 18px 24px 0;

`;
const Btn = styled.button`
  border: 1px solid #F7FFFA;
  background: transparent;
  color: #F7FFFA;
  cursor: pointer;
  &.active{
    background: linear-gradient(180deg, #62E99D 0%, #0ABF59 100%);
    color: #000;
    border: 0;
  }

`;

const ImgB = styled.img`
  display: inline-block;
  margin-bottom: -6px;
`;
const Rht = styled.div`
  display: flex;
  justify-content: space-between;
  align-content: center;
    @media (max-width: 768px) {
       display: none;
  }
  &.showRht{
    background: rgba(255,255,255,.89);
    z-index: 9;
    width: 100vw;
    height: calc(100vh - 80px);
    position: fixed;
    left: 0;
    top: 80px;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;
    padding: 24px;
    &>div{
      flex-direction: column; 
    }
    button{
     width: 100%;
     margin-left: 0;
      margin-bottom: 24px;
    }
    .order2{
    order:0;
    width: 1px;
    background: #CDCED4;
    }
  }
`;
const RhtIcon = styled.div`
  margin-top: 10px;
    @media (min-width: 769px) {
       display: none;
  }
`;
const BtmBefore = styled.div`
    display: flex;
    justify-content: space-between;
    align-content: center;
    white-space: nowrap;
   @media (max-width: 768px) {
       order: 2;
  }
`;
const Lines = styled.div`
    width: 12px;
   height: 1px;
   background: #0BBF59;
   margin: auto 10px;
    @media (max-width: 768px) {
    background: #CDCED4;
  }
`;
const Lftdiv = styled.div`
    margin-right: 20px;
    display: flex;
    align-content: center;
`;
const Rhtdiv = styled.div`
    display: flex;
    align-content: center;
`;

const Content = styled.div`
  margin-left: 10px;
  font-family: "Helvetica-Medium";
    //color: #1F2033;
    font-size: 16px;
    font-weight: 700;
    line-height: 40px;
`;
const Title = styled.div`
  margin-left: 10px;
  line-height: 40px;
  font-size: 12px;
  font-family: "helveticaneue-light";
`;

export default function Headertop(props) {

    const { network } = window.GeneralConfig;

    const {dispatch,state} = useWeb3();

    const {web3InstanceState,web3Instance,erc20contract,maincontract,Account,mybalance} = state;

    const [show, setShow] = useState(true);
    const [accountAddress, setAccountAddress] = useState('');
    const [showRht, setshowRht] = useState(false);
    const [type, setType] = useState('home');
    const [showTest, setShowTest] = useState(false);
    // const [balance, setBalance] = useState(null);

    useEffect(() => {
        setType(createHashHistory.location.pathname === '/my'?'my':'home')
        createHashHistory.listen((obj) => {
            setType(createHashHistory.location.pathname === '/my'?'my':'home')
        });
        let {test} = GetRequest();
        setShowTest(test)
        if(!window.ethereum){
            dispatch({type: 'SET_LOADING',payload:{
                    tips:"Please install MetaMask",
                }});
        }else{
            window.ethereum.on('chainChanged', (chainId) => {
                window.location.reload()
            });
            window.ethereum.on('accountsChanged', function (arr) {

                dispatch({type: 'SET_ALLACCOUNT',payload:null});
                sessionStorage.removeItem('account');
                createHashHistory.push('/');
                window.location.reload()
            });
        }

    }, []);
    const  GetRequest = () => {
        const url = window.location.hash;
        let theRequest = {};
        if (url.indexOf("?") !== -1) {
            let str = url.split('?')[1];
            let strs = str.split("&");
            for(let i = 0; i < strs.length; i ++) {
                theRequest[strs[i].split("=")[0]]=unescape(strs[i].split("=")[1]);
            }
        }
        return theRequest;
    }

    const getChainId = async() => {

        const chainId = await web3Instance.web3Instance.eth.getChainId();
        console.log(chainId)
        if((chainId === 1 && network === "mainnet") || (chainId === 42 && network === "kovan")){
            dispatch({type: 'SET_LOADING',payload:null});
        }else{
            dispatch({type: 'SET_LOADING',payload:{tips:"Please switch to the right network"}});
        }
    };

    useEffect(() => {
        if(web3InstanceState !== 'READY' || web3Instance == null) return;
        dispatch({type: 'LOAD_MAINCONTRACT'});
        dispatch({type: 'LOAD_ERC20CONTRACT'});
        getChainId();
    }, [dispatch,web3InstanceState]);

    useEffect(() => {
        if(erc20contract == null)return;
        let selectedStorage = sessionStorage.getItem('account');
        if (selectedStorage) {
            dispatch({type: 'SET_ALLACCOUNT',payload:selectedStorage});
            setAccountAddress(selectedStorage);
            dispatch({type: 'BALANCE_INIT'});
            setShow(false);

        }
    }, [Account,erc20contract]);


    const AddresstoShow = (address)=> {

        let frontStr = address.substring(0,4);

        let afterStr = address.substring(address.length-4,address.length);

        return `${frontStr}...${afterStr}`

    }
    const handleSwitch = (url)=> {
        setType(url);
        createHashHistory.push(`/${url}`);
        setshowRht(false)
    }
    const handleConnect = async () =>{
        setshowRht(!showRht);
    }

    const ConnectWallet = async () =>{
        const accoutlist = await Accounts.account(web3Instance.web3Instance);

        if(accoutlist.type === "success"){
            setAccountAddress(accoutlist.data);
            dispatch({type: 'SET_ALLACCOUNT',payload:accoutlist.data});
            setShow(false);
            dispatch({type: 'BALANCE_INIT'});
        } else {
            console.error("Please install MetaMask")
        }
    }

    const RegService = async () =>{

        services.regService(maincontract, Account).then((data) => {
            console.log("=====registerService", data);
        })

    }

    return (<HeaderBg>
        <div className="maincontain">
            <HeaderFlex>
                <img src={logo} alt=""/>
                <RhtIcon onClick={()=>handleConnect()}>
                    <img src={IconImg} alt=""/>
                </RhtIcon>
                <Rht className={showRht?'showRht':''}>
                    {
                        show && <Btn onClick={()=>ConnectWallet()} >Connect Wallet</Btn>
                    }
                    {
                        !show && <BtmBefore className="order">
                            <Lftdiv>
                                <img src={accountImg} alt=""/>

                                <Title>Address</Title>
                                <Lines/>
                                <Content>{AddresstoShow(accountAddress)}</Content>
                            </Lftdiv>
                            <Rhtdiv>
                                <img src={balanceImg} alt=""/>

                                <Title>Balance</Title>
                                <Lines />
                                <div>
                                    {
                                        mybalance == null &&<Loading />
                                    }
                                    {
                                        mybalance != null && <Content>{PublicObj.ToThousands(mybalance)} ZHT</Content>
                                    }
                                </div>

                            </Rhtdiv>
                        </BtmBefore>
                    }
                    <Btn onClick={()=>handleSwitch('home')} className={ type === 'home' ? 'active' : ''}>Servies <ImgB src={ type === 'home' ?arrowBlack:arrowWhite} alt=""/></Btn>
                    {
                        !show && <Btn onClick={()=>handleSwitch('my')}  className={ type === 'my' ? 'active' : ''}>My States<ImgB src={ type === 'my' ?arrowBlack:arrowWhite} alt=""/></Btn>
                    }

                    {
                        showTest && <Btn onClick={()=>RegService()}>Test Reg Service<ImgB src={ showTest ?arrowBlack:arrowWhite} alt=""/></Btn>
                    }

                </Rht>
            </HeaderFlex>
        </div>
    </HeaderBg>);

}

