import styled from "styled-components";
import { Avatar } from "@geist-ui/react";
import PublicObj from "../utils/publicJs";
import { useWeb3 } from "../api/contracts";
import services from "../api/services";




const Librdr = styled.div`
 
  width: 100%;
  border-radius: 8px;
  padding: 24px;
  box-sizing: border-box;
`;
const FirstRow = styled.div`
  display: flex;
  justify-content: space-between;
  align-content: center;
  border-bottom: 1px solid #E6E6EB;
  padding-bottom: 16px;
 margin-bottom: 24px;
  font-size: 12px;
`;
const Author = styled.div`
  display: inline-block;
  line-height: 28px;
  margin-left: 12px;
`;

const LftT = styled.div`
  display: flex;
  align-content: center;
`;
const Li = styled.li`
  margin-top: 24px;
`;

const Titles = styled.div`
    font-size: 16px;
    font-family: SF-Pro-Display-Bold;
`;
const TitleName = styled.div`
  display: flex;
  justify-content: flex-start;
  align-content: center;
 margin-bottom: 16px;
`;
const Star = styled.span`
  color: #e61717;
  font-weight: bolder;
  padding-top: 1px;
`;
const Names = styled.span`
    padding-bottom:4px;
    font-size: 12px;
    font-weight: 400;
    margin-left: 8px;
`;
const NumS = styled.span`
    font-size: 18px;
    line-height: 24px;
    font-family: d-din-bold;
`;


const Btn = styled.button`
      background: linear-gradient(180.25deg, rgba(36,114,244,1) 0%, rgba(50,65,244,1) 100%);
      border: 0;
      color: #ffffff;
      width: 100%;
      height: 32px;
      margin-left: 0;
      cursor: pointer;
        &:disabled,
      &[disabled]{
      background: #CDCED4;
      color: #fff;
    }
`;
// const RewardName = styled.span`
//   margin-right: 8px;
//   font-size: 14px;
// `;
// const Li24 = styled.div`
//   margin-bottom: 24px;
// `;


export default function MyItem(props) {
    const { state, dispatch } = useWeb3();
    const { maincontract, web3Instance, Account } = state;
    const { handleRefresh, Itemdata: {
        spname, logo, name,
        // myStake,
        status,
        // myTotal,
        myAvailable,
        // myClaimed, threshold,
        created, period, grace, id } } = props;


    const toThousands = (str) => {
        let balance = web3Instance.web3Instance.utils.fromWei(str);
        return PublicObj.ToThousands(balance);
    }

    const handleclaim = () => {

        dispatch({ type: 'SET_LOADING', payload: { tips: "Claim..." } });
        services.claim(maincontract, id, Account).then(() => {
            dispatch({ type: 'SET_LOADING', payload: null });
            dispatch({ type: 'BALANCE_INIT' });
            handleRefresh();
        }).catch(() => {
            dispatch({ type: 'SET_LOADING', payload: null });
            dispatch({ type: 'BALANCE_INIT' });
            handleRefresh();
        });
    }
    const handleRedeem = () => {
        dispatch({
            type: 'SET_LOADING', payload: {
                tips: "Redeem..."
            }
        });
        services.redeem(maincontract, id, Account).then(() => {
            dispatch({ type: 'SET_LOADING', payload: null });
            dispatch({ type: 'BALANCE_INIT' });
            handleRefresh();
        }).catch(() => {
            dispatch({ type: 'SET_LOADING', payload: null });
            dispatch({ type: 'BALANCE_INIT' });
            handleRefresh();
        });
    }

    const switchTime = (dateTime) => {
        const daytime = parseInt(dateTime) * 1000;
        return new Date(daytime).toLocaleString("en");
    }

    const switchEndTime = (dateTime, grace) => {
        const times = parseInt(dateTime) + parseInt(grace);
        return switchTime(times);
    }


    return (
        <Librdr>
            <FirstRow>
                <LftT>
                    <Avatar src={logo} width="28px" />
                    <Author>by {spname}</Author>
                </LftT>

            </FirstRow>
            <div>
                <Titles> {name}</Titles>
                <ul>
                    <Li>
                        <TitleName> <Star>*</Star><Names>Finish Time</Names></TitleName>
                        <div>
                            <NumS>{switchEndTime(created, grace+period)}</NumS>
                        </div>
                    </Li>
                    {/*<Li>*/}
                    {/*    <TitleName> <Star>*</Star><Names>My Contribute</Names></TitleName>*/}
                    {/*    <div>*/}
                    {/*        <NumS>{toThousands(myStake)} ZHT</NumS>*/}
                    {/*    </div>*/}
                    {/*</Li>*/}
                    {/*<Li>*/}
                    {/*    <TitleName> <Star>*</Star><Names>My Rewards</Names></TitleName>*/}
                    {/*    <Li24><RewardName>Total:</RewardName><NumS>{toThousands(myTotal)} ZHT</NumS></Li24>*/}
                    {/*    <Li24><RewardName>Available:</RewardName><NumS>{toThousands(myAvailable)} ZHT</NumS></Li24>*/}
                    {/*    <Li24><RewardName>Claimed:</RewardName><NumS>{toThousands(myClaimed)} ZHT</NumS></Li24>*/}
                    {/*</Li>*/}
                </ul>
            </div>
            <div>
                {/*0 created, 1 in progress, 2 finished, 3 failed.*/}
                {
                    status === '0' && <Btn disabled>Claim</Btn>
                }
                {
                    status === '1' && <Btn onClick={() => handleclaim(id)}>Claim {toThousands(myAvailable)} ZHT</Btn>
                }
                {
                    (status === '2' || status === '3') && <Btn onClick={() => handleRedeem(id)} >Redeem</Btn>
                }
            </div>
        </Librdr>
    );

}
