import PublicObj from "../utils/publicJs";
const pinataSDK = require('@pinata/sdk');

async function initPinata() {
    const pinata = pinataSDK("2888930acbe7044e783b", "5117b40658a87dfea5ec9668252c00d732b4bc730197ec2343317becd1ecaf9d");
    await pinata.testAuthentication();
    return pinata;
}

async function publishJSONToPinata(pinata, data, name) {
    const { IpfsHash } = await pinata.pinJSONToIPFS(data, {
        pinataMetadata: { name },
    });
    return IpfsHash;
}

const getServicesList = async (contractInstance, account) => {
    let list = await contractInstance.methods.getServices().call({ from: account });
    console.log("======getServicesList", list)


    let promises = list.map(async (service) => { return await PublicObj.getServiceProviderInfo(service.key); });
    let results = await Promise.all(promises);

    console.log("=======get All services info", results);

    for (let index = 0; index < results.length; index++) {
        results[index] = Object.assign(results[index], list[index]);
    }

    return results;
}

const getServiceById = async (contractInstance, serviceId, account) => {
    let service = await contractInstance.methods.getServiceById(serviceId).call({ from: account });

    console.log("=======getServiceById", service);

    let serviceInfo = await PublicObj.getServiceProviderInfo(service.key);
    console.log("=======ipfs info", serviceInfo);

    service = Object.assign(serviceInfo, service);

    console.log("=======merged", service);

    return service;
}

const regService = async (contractInstance, account) => {
    let timestamp = Date.now();
    let service, hash;

    // #1
    service = {
        name: "Kovan Testnet Service-" + timestamp,
        spname: "Me",
        share: 88,
        type: 1,
        logo: "https://avatars.githubusercontent.com/u/26024178?v=4",
        desc: "This is the service for Kovan Testnet. Please feel free to use it."
    }


    const pinata = await initPinata();
    hash = await publishJSONToPinata(pinata, service, `sp-info-${timestamp}-${service.spname}-${service.name}`);

    let tx = await contractInstance.methods.regService(account, 88, 1, hash).send({ from: account });
    console.log("regService====", tx);
}


const stake = async (contractInstance, web3Instance, serviceId, amount, account) => {
    let ret = await contractInstance.methods.stake(serviceId, web3Instance.web3Instance.utils.toWei(amount.toString())).send({ from: account });
    console.log(ret);
    return ret;
}

const approve = async (contractInstance, web3Instance, amount, account, spender) => {
    let ret = await contractInstance.methods.approve(spender, web3Instance.web3Instance.utils.toWei(amount.toString())).send({ from: account });
    return ret;
}

