import AddressObj from "./addressConfig";

const {erc20ABI, erc20Address} = AddressObj;

let erc20contract = null;
let loadERC20 = false;

export default  async function initERC20Contract (state, dispatch) {
    const {web3Instance,erc20contractState} = state;

    if(web3Instance == null || web3Instance.web3Instance == null) return;

    const asyncLoadERC20 = async () => {
        try {
            erc20contract = await new web3Instance.web3Instance.eth.Contract(erc20ABI, erc20Address);
            dispatch({type: 'SET_ERC20CONTRACT', payload: erc20contract});
        } catch (e) {
            console.error(e);
            dispatch({type: 'ERC20CONTRACT_ERROR'});
        }
    };
    if (erc20contractState !== 'LOAD_ERC20CONTRACT') return;
    if (loadERC20) return dispatch({type: 'SET_ERC20CONTRACT', payload: erc20contract});
    loadERC20 = true;
    asyncLoadERC20();
}
