const reducer = (state, action) => {
    switch (action.type) {
        //account
        case 'LOAD_ALLACCOUNT':
            return { ...state, accountState: 'LOAD_ALLACCOUNTS', Account: null };

        case 'SET_ALLACCOUNT':
            return { ...state, Account: action.payload, accountState: 'READY' };

        case 'ALLACCOUNT_ERROR':
            return { ...state, Account: null, accountState: 'ERROR' };

        //main contract
        case 'LOAD_MAINCONTRACT':
            return { ...state, maincontractState: 'LOAD_MAINCONTRACT' };

        case 'SET_MAINCONTRACT':
            return { ...state, maincontract: action.payload, maincontractState: 'READY' };

        case 'MAINCONTRACT_ERROR':
            return { ...state, maincontract: null, maincontractState: 'ERROR' };

        //erc20 contract
        case 'LOAD_ERC20CONTRACT':
            return { ...state, erc20contractState: 'LOAD_ERC20CONTRACT' };

        case 'SET_ERC20CONTRACT':
            return { ...state, erc20contract: action.payload, erc20contractState: 'READY' };

        case 'ERC20CONTRACT_ERROR':
            return { ...state, erc20contract: null, erc20contractState: 'ERROR' };


        //web3Instance
        case 'CONNECT_INIT':
            return { ...state, web3InstanceState: 'CONNECT_INIT' };

        case 'CONNECT':
            return { ...state, web3Instance: action.payload, web3InstanceState: 'READY' };

        case 'CONNECT_ERROR':
            return { ...state, web3InstanceState: 'ERROR', web3InstanceError: action.payload };

        case 'BALANCE_INIT':
            return { ...state, mybalanceState: 'BALANCE_INIT' };
        case 'SET_BALANCE':
            return { ...state, mybalance: action.payload,mybalanceState:'READY' };
        case 'BALANCE_ERROR':
            return { ...state, mybalanceState: 'ERROR'};

        case 'SET_LOADING':
            return { ...state, loading: action.payload };


        default:
            throw new Error(`Unknown type: ${action.type}`);
    }
};
export default reducer
